@import "../styles/variables";

.projects-container {
    transform: translate(0, -5px);
    background-color: $c-main-dark;
    color: $c-main-light;
    h1 {
        padding-top: 48px;
        margin: 0 0 38px 0;
        font-family: $ff-header;
        font-weight: 400;
        text-align: center;
        font-size: 32px;
    }
    .project-list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    button {
        margin: 0 auto;
    }
    @media (min-width: $tablet-s) {
        h1 {
            padding-top: 56px;
            margin: 0 0 56px 0;
            font-size: 40px;
        }
        button {
            margin: 0 auto;
        }
    }
    @media (min-width: $laptop-s) {
        h1 {
            font-size: 42px;
        }
        .project-list {
            flex-direction: row;
            gap: 16%;
        }
        button {
            margin: 0 auto;
        }
    }
}